.menu_container {
  background-color: var(--aside-background);
  height: 100vh;
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  transform: translateX(-120%);
  transition: ease-out 0.5s;
  z-index: 100;
}

.menu_container__active {
  background-color: var(--aside-background);
  height: 100vh;
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  transform: translateX(0%);
  transition: ease-in 0.5s;
  z-index: 4;
  position: fixed;
}

.menu {
  margin-left: 128px;
  display: flex;
  flex-direction: column;
  margin-bottom: 38px;
}

.close_menu {
  background-color: unset;
  padding: 0;
  border: none;
  cursor: pointer;
  margin-left: 24px;
  margin-top: 32px;
  position: absolute;
  top: 0;
}

.menu_link {
  text-decoration: none;
  color: var(--white);
  font-weight: 500;
  font-size: 32px;
  line-height: 38px;
  text-align: left;
}

.menu_link:hover {
  color: var(--main-accent);
}

.menu_link:active {
  color: var(--main-accent);
}

.social_media {
  margin-right: 24px;
  margin-left: 128px;
}

.social_media__item {
  margin-right: 24px;
}

@media (min-width: 2042px) {
  .menu_container__active {
    position: fixed;
    min-height: 100%;
    width: 60%;
  }
}

@media (max-width: 1023px) {
  .menu_container__active {
    width: 100%;
  }

  .menu_link {
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 33px;
  }
}

@media (max-width: 767px) {
  .menu_container {
    justify-content: flex-start;
    transform: translateX(-200%);
  }

  .menu_container__active {
    justify-content: flex-start;
  }
  .close_menu {
    margin-top: 16px;
    margin-left: 20px;
  }

  .menu {
    margin-left: 28px;
    margin-top: 80px;
  }

  .menu_link {
    font-weight: 500;
    font-size: 22px;
    line-height: 26px;
  }

  .social_media {
    margin-left: 28px;
  }
}
